@media (max-width: 500px) {
    padding: 16px 16px 50px;
}

@media only screen and (max-width: 2160px) {
  nav > ul {
	  margin: 2rem 0;
	  padding: 0;
	  list-style-type: none;
	  display: flex;
	  justify-content: center;
	  gap: 1.5rem;
	  font-size: 1.3rem;
	  font-weight: bold;
	  line-break: auto;
  }
}

@media only screen and (max-width: 477px) {
  nav > ul {
	  margin: 1.1rem 0;
	  padding: 0;
	  list-style-type: none;
	  display: flex;
	  justify-content: center;
	  gap: 1.2rem;
	  font-size: 0.9rem;
	  font-weight: bold;
	  line-break: auto;
  }
}


@media only screen and (max-width: 368px) {
  nav > ul {
	  margin: 1.0rem 0;
	  padding: 0;
	  list-style-type: none;
	  display: flex;
	  justify-content: center;
	  gap: 1.1rem;
	  font-size: 0.5rem;
	  font-weight: bold;
	  line-break: auto;
  }
}

